import { useState } from "react";

export default function ProgramizStyleIDE() {
  const [code, setCode] = useState("");
  const [sections, setSections] = useState({ output: "", assembly: "", machine: "" });
  const [selected, setSelected] = useState("output");

  const examples = {
    basic: `sugod
  ipakita: "Hi, Bisaya++";
  ipakita: "Bisaya++ Interpreter";
katapusan`,
    arithmetic: `sugod
  mugna a, b, c;
  mugna b = 10;
  mugna c = 20;
  mugna a = b+c+c+b;
  ipakita: a;
katapusan`,
    multiply: `sugod
  ipakita: 10*2;
katapusan`,
  };

  const runCode = async () => {
    if (!code.trim()) return;

    const res = await fetch("/api/parse", { method: "POST", body: code });
    const data = await res.json();

    if (data.error) {
      setSections({ output: data.error, assembly: "", machine: "" });
      return;
    }

    const text = data.output;
    const extract = (start, end) => {
      const s = text.indexOf(start);
      if (s === -1) return "";
      const e = text.indexOf(end, s + start.length);
      if (e === -1) return text.slice(s + start.length).trim();
      return text.slice(s + start.length, e).trim();
    };

    setSections({
      output: extract("=== STARTING COMPILATION ===", "=== MIPS64 ASSEMBLY CODE ==="),
      assembly: extract("=== MIPS64 ASSEMBLY CODE ===", "=== MACHINE CODE ==="),
      machine: extract("=== MACHINE CODE ===", "=== COMPILATION COMPLETE ==="),
    });

    setSelected("output");
  };

  const clearAll = () => {
    setCode("");
    setSections({ output: "", assembly: "", machine: "" });
    setSelected("output");
  };

  const loadExample = (name) => setCode(examples[name]);

  const displayed = sections[selected];

  return (
    <main className="min-h-screen bg-gray-100 text-gray-900 p-6">
      {/* HEADER */}
      <div className="bg-white p-4 rounded-md shadow border border-gray-200 mb-4">
        <h1 className="text-2xl font-bold">Bisdak Compiler IDE</h1>
        <p className="text-sm text-gray-600">Programiz-style Web IDE</p>
      </div>

      {/* GRID */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* LEFT PANEL */}
        <div className="bg-white p-4 rounded-lg shadow border border-gray-200">
          <div className="flex justify-between items-center mb-2">
            <h2 className="text-lg font-semibold">main.bsd</h2>
            <button className="px-4 py-1 bg-blue-600 hover:bg-blue-700 text-white rounded" onClick={runCode}>Run</button>
          </div>

          <textarea
            value={code}
            onChange={(e) => setCode(e.target.value)}
            className="w-full h-96 bg-gray-900 text-green-300 p-3 rounded-lg font-mono text-sm border border-gray-700"
            placeholder="Type your Bisaya++ code here..."
          />

          {/* Bottom Buttons */}
          <div className="flex gap-3 mt-3">
            <button onClick={clearAll} className="px-4 py-2 bg-red-600 hover:bg-red-700 text-white rounded">Clear</button>

            <div className="relative group">
              <button className="px-4 py-2 bg-green-600 hover:bg-green-700 text-white rounded">Examples â–¼</button>
              <div className="absolute hidden group-hover:block mt-1 w-48 bg-white border border-gray-300 rounded shadow">
                <button onClick={() => loadExample("basic")} className="block text-left w-full px-3 py-2 hover:bg-gray-100">Basic Example</button>
                <button onClick={() => loadExample("arithmetic")} className="block text-left w-full px-3 py-2 hover:bg-gray-100">Arithmetic Example</button>
                <button onClick={() => loadExample("multiply")} className="block text-left w-full px-3 py-2 hover:bg-gray-100">Multiply Example</button>
              </div>
            </div>
          </div>
        </div>

        {/* RIGHT PANEL */}
        <div className="bg-white p-4 rounded-lg shadow border border-gray-200">
          <h2 className="text-lg font-semibold mb-3">Output</h2>

          <div className="flex gap-3 mb-4">
            {['output', 'assembly', 'machine'].map((tab) => (
              <button
                key={tab}
                onClick={() => setSelected(tab)}
                className={`px-4 py-2 rounded ${selected === tab ? 'bg-blue-600 text-white' : 'bg-gray-300 text-gray-800'}`}
              >
                {tab === 'output' ? 'Output' : tab === 'assembly' ? 'Assembly' : 'Machine Code'}
              </button>
            ))}
          </div>

          <pre className="w-full h-96 bg-gray-900 text-yellow-300 p-3 overflow-auto rounded font-mono text-sm border border-gray-700">
            {displayed}
          </pre>
        </div>
      </div>
    </main>
  );
}